import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import { NaverMap, RenderAfterNavermapsLoaded, Marker } from 'react-naver-maps';
import { useState, useEffect, useRef } from 'react';
import 'assets/css/groupsAddUpdate.css';
import 'assets/css/stationAddOrUpdate.css';
import { useDispatch, useSelector } from 'react-redux';
import { showMessage } from 'app/store/fuse/messageSlice';
import { DetailTitleBar } from 'app/theme-layouts/mainLayout/components';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { open, close } from 'app/store/common/loadingWrap';
import { selectComboBox } from 'app/store/common/comboBox';
import { Modal } from 'semantic-ui-react';
import classnames from 'classnames';
import { selectStations, setStations } from 'app/store/common/stations';

function AddOrUpdate() {
  const location = useLocation();
  const param = location.state;
  const companyComboBox = useSelector(selectComboBox).company;
  const projectComboBox = useSelector(selectComboBox).project;
  const groupComboBox = useSelector(selectComboBox).group;
  const manufacturerComboBox = useSelector(selectComboBox).manufacturer;
  const otaComboBox = useSelector(selectComboBox).otaVersion;
  const stationStateComboBox = useSelector(selectComboBox).stationState;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const deviceName = useRef();
  const deviceDesc = useRef();
  const searchNameInput = useRef();
  const [projectCategory, setProjectCategory] = useState([]);
  const [companyCategory, setCompanyCategory] = useState([]);
  const [otaCategory, setOtaCategory] = useState([]);
  const [groupCategory, setGroupCategory] = useState([]);
  const [manufacturerCategory, setManufacturerCategory] = useState([]);
  const [deviceStateCategory, setDeviceStateCategory] = useState([]);
  const [projectId, setProjectId] = useState('');
  const [companyId, setCompanyId] = useState('');
  const [otaVer, setOtaVer] = useState('');
  const [groupId, setGroupId] = useState('');
  const [manufacturer, setManufacturer] = useState('');
  const [deviceState, setDeviceState] = useState('');
  const [validation, setValidation] = useState([]);
  const [address, setAddress] = useState('');
  const [popOpen, setPopOpen] = useState(false);
  const [lat, setLat] = useState('');
  const [long, setLong] = useState('');
  const [keywordObj, setKeywordObj] = useState({});
  const [keywordList, setKeywordList] = useState([]);
  const dataList = useSelector(selectStations).stations;

  useEffect(() => {
    setOtaCategory(otaComboBox);
    setManufacturerCategory(manufacturerComboBox);
    setCompanyCategory(companyComboBox);
    setDeviceStateCategory(stationStateComboBox);
    if (param.detailLink[0].title === '추가') {
      setCompanyId(companyComboBox[0].code);
      setOtaVer(otaComboBox[0].code);
      setManufacturer(manufacturerComboBox[0].code);
      setDeviceState(stationStateComboBox[0].code);
      const projectArr = projectComboBox.filter((obj) => obj.parentId === companyComboBox[0].code);
      setProjectCategory(projectArr);
      setProjectId(projectComboBox[0].code);
      setGroupCategory(groupComboBox.filter((obj) => obj.parentId === projectArr[0].code));
      setGroupId(groupComboBox[0].code);
    } else if (param.detailLink[0].title === '수정') {
      setCompanyId(param.companyId);
      setOtaVer(param.otaSeq);
      setManufacturer(param.manufacturer);
      setDeviceState(param.deviceState);
      setProjectCategory(projectComboBox.filter((obj) => obj.parentId === param.companyId));
      setProjectId(param.projectId);
      setGroupCategory(groupComboBox.filter((obj) => obj.parentId === param.projectId));
      setGroupId(param.groupId);
      setAddress(param.address);
      setLat(param.latitude);
      setLong(param.longitude);
    }
  }, []);

  const saveBtnClick = () => {
    confirmAlert({
      title: `스테이션 정보를 ${param.detailLink[0].title} 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            if (address === '' || lat === '' || long === '') {
              dispatch(
                showMessage({ message: '스테이션 위치 정보를 입력해주세요.', variant: 'error' })
              );
              return;
            }
            const deviceObj = {};
            deviceObj.companyId = companyId;
            deviceObj.projectId = projectId;
            deviceObj.groupId = groupId;
            deviceObj.deviceName = deviceName.current.value;
            deviceObj.deviceDesc = deviceDesc.current.value;
            deviceObj.manufacturer = manufacturer;
            deviceObj.deviceState = deviceState;
            deviceObj.operationConfigSeq = otaVer;
            deviceObj.address = address;
            deviceObj.latitude = lat;
            deviceObj.longitude = long;
            console.log(deviceObj);
            dispatch(open());

            const filter1 = dataList.filter((obj) => obj.projectId === projectId);
            const filter2 = filter1.filter((obj) => obj.groupName === deviceName.current.value);

            if (filter2.length > 0) {
              dispatch(showMessage({ message: '장치 정보 저장 실패.', variant: 'error' }));
              setValidation([{ type: 'deviceName', message: '이미 존재하는 장치명입니다.' }]);
              dispatch(close());
              return;
            }
            const today = new Date();
            const year = today.getFullYear();
            const month = `0${today.getMonth() + 1}`.slice(-2);
            const day = `0${today.getDate()}`.slice(-2);
            const dateString = `${year}-${month}-${day}`;
            const hours = `0${today.getHours()}`.slice(-2);
            const minutes = `0${today.getMinutes()}`.slice(-2);
            const seconds = `0${today.getSeconds()}`.slice(-2);
            const timeString = `${hours}:${minutes}:${seconds}`;
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.deviceId !== param.deviceId);
            switch (param.detailLink[0].title) {
              case '추가':
                clone.push({
                  companyId,
                  companyName: '(주)한빛',
                  deviceId: `BSS${year}${month}${day}${minutes}${seconds}`,
                  deviceName: deviceName.current.value,
                  deviceDesc: deviceDesc.current.value,
                  deviceState,
                  deviceStateName: stationStateComboBox.filter((obj) => obj.code === deviceState)[0]
                    .codeName,
                  manufacturer,
                  manufacturerName: manufacturerComboBox.filter(
                    (obj) => obj.code === manufacturer
                  )[0].codeName,
                  address,
                  latitude: lat,
                  longitude: long,
                  projectId,
                  projectName: projectComboBox.filter((obj) => obj.code === projectId)[0].codeName,
                  groupId,
                  groupName: groupComboBox.filter((obj) => obj.code === groupId)[0].codeName,
                  errorStatus: '0',
                  errorStatusName: '정상',
                  errorCnt: 0,
                  swVersion: '1.1.0.3',
                  updateUsername: '한빛 관리자',
                  operationConfigSeq: '2',
                  operationConfigName: '개발용 운영정보',
                  updateTime: `${dateString} ${timeString}`,
                  createTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setStations({ data: clone }));
                dispatch(
                  showMessage({ message: '스테이션 정보가 추가되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/deviceManagement/stations');
                break;
              case '수정':
                filter.push({
                  companyId,
                  companyName: '(주)한빛',
                  deviceId: param.deviceId,
                  deviceName: deviceName.current.value,
                  deviceDesc: deviceDesc.current.value,
                  deviceState,
                  deviceStateName: stationStateComboBox.filter((obj) => obj.code === deviceState)[0]
                    .codeName,
                  manufacturer,
                  manufacturerName: manufacturerComboBox.filter(
                    (obj) => obj.code === manufacturer
                  )[0].codeName,
                  address,
                  latitude: lat,
                  longitude: long,
                  projectId,
                  projectName: projectComboBox.filter((obj) => obj.code === projectId)[0].codeName,
                  groupId,
                  groupName: groupComboBox.filter((obj) => obj.code === groupId)[0].codeName,
                  errorStatus: '0',
                  errorStatusName: '정상',
                  errorCnt: 0,
                  swVersion: '1.1.0.3',
                  updateUsername: '한빛 관리자',
                  operationConfigSeq: '2',
                  operationConfigName: '개발용 운영정보',
                  updateTime: `${dateString} ${timeString}`,
                  createTime: param.createTime,
                });
                dispatch(close());
                dispatch(setStations({ data: filter }));
                dispatch(
                  showMessage({ message: '스테이션 정보가 수정되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/deviceManagement/stations');
                break;
              default:
            }
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  const deleteBtnClick = () => {
    confirmAlert({
      title: `장비 정보를 삭제 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            dispatch(open());
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.deviceId !== param.deviceId);
            dispatch(close());
            dispatch(setStations({ data: filter }));
            dispatch(
              showMessage({ message: '스테이션 정보가 삭제되었습니다.', variant: 'success' })
            );
            navigate('/bss_server/deviceManagement/stations');
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  const searchBtnClick = () => {
    // `https://dapi.kakao.com/v2/local/search/address.json?query=${searchNameInput.current.value}`
    setPopOpen(true);
    fetch(
      `https://dapi.kakao.com/v2/local/search/keyword.json?query={${searchNameInput.current.value}}`,
      { headers: { Authorization: 'KakaoAK f2fc184468185edbf1e0a16114960df5' } }
    )
      .then((response) => response.json())
      .then((response) => {
        setKeywordObj({});
        if (Object.prototype.hasOwnProperty.call(response, 'documents')) {
          setKeywordList(response.documents);
        } else {
          setKeywordList([]);
        }
      });
  };

  return (
    <div className="section__grid__wrap content groups__add__update station__add__update">
      <DetailTitleBar
        flag={param.detailLink[0].title}
        deleteBtnClick={deleteBtnClick}
        saveBtnClick={saveBtnClick}
      />
      <Modal onClose={() => setPopOpen(false)} onOpen={() => setPopOpen(true)} open={popOpen}>
        <div className="content__header">
          <h3 className="f__medium">
            '<span>{popOpen ? searchNameInput.current.value : ''}</span>'&nbsp;&nbsp;위치 검색
          </h3>
          <h4 className="f__medium">
            <span>{keywordList.length}</span>건의 데이터
          </h4>
        </div>
        <div id="stationDetail" className="content__body vertical__scroll">
          <div className="item__list vertical__scroll">
            {keywordList.map((obj) => (
              <ul
                className={classnames({ selected: keywordObj.x === obj.x })}
                key={obj.x}
                onClick={(e) => {
                  setKeywordObj(obj);
                }}
              >
                <li>
                  <dl>
                    <dt className="f__medium">주소&nbsp;:&nbsp;</dt>
                    <dd>{obj.address_name}</dd>
                  </dl>
                </li>
                <li>
                  <dl>
                    <dt className="f__medium">위도&nbsp;:&nbsp;</dt>
                    <dd>{obj.y}</dd>
                  </dl>
                </li>
                <li>
                  <dl>
                    <dt className="f__medium">경도&nbsp;:&nbsp;</dt>
                    <dd>{obj.x}</dd>
                  </dl>
                </li>
              </ul>
            ))}
          </div>
          {popOpen && (
            <RenderAfterNavermapsLoaded
              ncpClientId="qelkdx0v6n"
              error={<p>Maps Load Error</p>}
              loading={<p>Maps Loading...</p>}
            >
              <NaverMap
                center={{ lat: keywordObj.y, lng: keywordObj.x }}
                // defaultCenter={{ lat: keywordObj.y, lng: keywordObj.x }}
                defaultZoom={14}
              >
                <Marker position={{ lat: keywordObj.y, lng: keywordObj.x }} />
              </NaverMap>
            </RenderAfterNavermapsLoaded>
          )}
        </div>
        <div className="btn__wrap">
          <button
            className="custom__btn"
            onClick={(e) => {
              setPopOpen(false);
            }}
          >
            <span className="f__medium">취소</span>
          </button>
          <button
            className="custom__btn"
            onClick={(e) => {
              if (keywordObj.x !== undefined && keywordObj.y !== undefined) {
                setAddress(keywordObj.address_name);
                setLat(keywordObj.y);
                setLong(keywordObj.x);
                setPopOpen(false);
              } else {
                //
              }
            }}
          >
            <span className="f__medium">확인</span>
          </button>
        </div>
      </Modal>
      {/* station__input__wrap  */}
      <section className="station__input__wrap">
        <div className="section__inner">
          <div className="input__wrap">
            <ul>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    회사명
                  </dt>
                  <dd>
                    <select
                      value={companyId}
                      className="custom__select"
                      onChange={(e) => {
                        setCompanyId(e.target.value);
                        setProjectCategory(
                          projectComboBox.filter((obj) => obj.parentId === e.target.value)
                        );
                        setProjectId(projectCategory[0].code);
                        setGroupCategory(
                          groupComboBox.filter((obj) => obj.parentId === projectCategory[0].code)
                        );
                        setGroupId(groupCategory[0].code);
                      }}
                    >
                      {companyCategory.map((obj, idx) => (
                        <option key={obj.code} value={obj.code}>
                          {obj.codeName}
                        </option>
                      ))}
                    </select>
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    프로젝트명
                  </dt>
                  <dd>
                    <select
                      value={projectId}
                      className="custom__select"
                      onChange={(e) => {
                        setProjectId(e.target.value);
                        setGroupCategory(
                          groupComboBox.filter((obj) => obj.parentId === e.target.value)
                        );
                        setGroupId(groupCategory[0].code);
                      }}
                    >
                      {projectCategory.map((obj, idx) => (
                        <option key={obj.code} value={obj.code}>
                          {obj.codeName}
                        </option>
                      ))}
                    </select>
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    운영 그룹
                  </dt>
                  <dd>
                    <select
                      value={groupId}
                      className="custom__select"
                      onChange={(evt) => {
                        setGroupId(evt.target.value);
                      }}
                    >
                      {groupCategory.map((obj, idx) => (
                        <option key={obj.code} value={obj.code}>
                          {obj.codeName}
                        </option>
                      ))}
                    </select>
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    제조사명
                  </dt>
                  <dd>
                    <select
                      value={manufacturer}
                      className="custom__select"
                      onChange={(e) => {
                        setManufacturer(e.target.value);
                      }}
                    >
                      {manufacturerCategory.map((obj, idx) => (
                        <option key={obj.code} value={obj.code}>
                          {obj.codeName}
                        </option>
                      ))}
                    </select>
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    OTA 버전
                  </dt>
                  <dd>
                    <select
                      value={otaVer}
                      className="custom__select"
                      onChange={(evt) => {
                        setOtaVer(evt.target.value);
                      }}
                    >
                      <option value="0">미적용</option>
                      {otaCategory.map((obj, idx) => (
                        <option key={obj.code} value={obj.code}>
                          {obj.codeName}
                        </option>
                      ))}
                    </select>
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    장치명
                  </dt>
                  <dd>
                    <input
                      ref={deviceName}
                      defaultValue={param.deviceName}
                      className="custom__input"
                      type="text"
                    />
                    {validation.map(
                      (obj) =>
                        obj.type === 'deviceName' && (
                          <p className="f__medium field__error">{obj.message}</p>
                        )
                    )}
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>설명</dt>
                  <dd>
                    <textarea
                      ref={deviceDesc}
                      defaultValue={param.deviceDesc}
                      className="custom__input"
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    장비상태
                  </dt>
                  <dd>
                    <select
                      value={deviceState}
                      className="custom__select"
                      onChange={(e) => {
                        setDeviceState(e.target.value);
                      }}
                    >
                      {deviceStateCategory.map((obj, idx) => (
                        <option key={obj.code} value={obj.code}>
                          {obj.codeName}
                        </option>
                      ))}
                    </select>
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>설정 값</dt>
                  <dd className="setting">
                    <select className="custom__select">
                      <option>전체</option>
                    </select>
                    <button className="custom__btn">
                      <span className="f__medium">설정</span>
                    </button>
                  </dd>
                </dl>
              </li>
              {param.detailLink[0].title === '수정' && (
                <li>
                  <dl>
                    <dt>Access Code</dt>
                    <dd>
                      <button className="custom__btn">
                        <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M12 6v6m0 0v6m0-6h6m-6 0H6"
                          />
                        </svg>
                        <span className="f__medium">Download</span>
                      </button>
                    </dd>
                  </dl>
                </li>
              )}
            </ul>
          </div>
          <div className="map__wrap">
            <dl>
              <dt>스테이션 설치 위치</dt>
              <dd>
                <input
                  ref={searchNameInput}
                  onKeyPress={(e) => e.key === 'Enter' && searchBtnClick()}
                  className="custom__input"
                />
                <button className="custom__btn" onClick={() => searchBtnClick()}>
                  <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth="2"
                      d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                    />
                  </svg>
                  <span className="f__medium">검색</span>
                </button>
              </dd>
            </dl>
            <div id="map">
              {!popOpen && (
                <RenderAfterNavermapsLoaded
                  ncpClientId="qelkdx0v6n"
                  error={<p>Maps Load Error</p>}
                  loading={<p>Maps Loading...</p>}
                >
                  <NaverMap center={{ lat, lng: long }} defaultZoom={16}>
                    <Marker
                      position={{ lat, lng: long }}
                      key={param.deviceId}
                      title={param.deviceName}
                      icon={{
                        content: `<div class="markerBox">
                <div class="title f__medium">${param.deviceName}</div>
                  <div class="marker__wrap">
                    <img src='${process.env.PUBLIC_URL}/images/icon/marker/${deviceState}.png' alt="${param.deviceStateName}"/>
                  </div>
                </div>`,
                      }}
                    />
                  </NaverMap>
                </RenderAfterNavermapsLoaded>
              )}
            </div>
            <ul>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    주소
                  </dt>
                  <dd>
                    <input
                      defaultValue={address}
                      className="custom__input readonly"
                      readOnly
                      type="text"
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    위도
                  </dt>
                  <dd>
                    <input
                      defaultValue={lat}
                      className="custom__input readonly"
                      readOnly
                      type="number"
                      step="0.1"
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <span className="f__bold" style={{ color: 'red' }}>
                      *&nbsp;
                    </span>
                    경도
                  </dt>
                  <dd>
                    <input
                      defaultValue={long}
                      className="custom__input readonly"
                      readOnly
                      type="number"
                      step="0.1"
                    />
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // station__input__wrap */}
    </div>
  );
}

export default AddOrUpdate;
